'use client';

import React, { useState, useEffect, useContext } from 'react';
import styled, { keyframes } from 'styled-components';
import { ThemeContext } from '@/theme/ThemeProvider';

const floatAnimation = keyframes`
  0% {
    transform: translateY(0) translateX(0);
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    transform: translateY(-120px) translateX(20px);
    opacity: 0;
  }
`;

const BubbleContainer = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  pointer-events: none;
  z-index: -1;
`;

interface BubbleProps {
  size: number;
  position: number;
  delay: number;
  duration: number;
  color: string;
}

const Bubble = styled.div<BubbleProps>`
  position: absolute;
  bottom: -20px;
  left: ${props => props.position}%;
  width: ${props => props.size}px;
  height: ${props => props.size}px;
  border-radius: 50%;
  background-color: ${props => props.color};
  opacity: 0;
  animation: ${floatAnimation} ${props => props.duration}s ease-in-out ${props => props.delay}s infinite;
  box-shadow: 0 0 10px ${props => props.color}33;
`;

export const AnimatedBackground: React.FC = () => {
  const [bubbles, setBubbles] = useState<React.ReactNode[]>([]);
  const { theme } = useContext(ThemeContext);
  
  useEffect(() => {
    // Use colors from the theme
    const colors = [
      theme.primary,
      theme.secondary,
      `${theme.primary}88`, // Semi-transparent primary
      `${theme.secondary}88`, // Semi-transparent secondary
      theme.isDark ? '#ffffff22' : '#00000022' // Light/dark contrast bubbles
    ];
    
    const newBubbles = Array.from({ length: 40 }).map((_, i) => {
      const size = Math.random() * 15 + 5; // Size between 5-20px
      const position = Math.random() * 100;
      const delay = Math.random() * 8;
      const duration = Math.random() * 10 + 10; // Duration between 10-20s for more subtle movement
      const color = colors[Math.floor(Math.random() * colors.length)];
      
      return (
        <Bubble 
          key={i}
          size={size}
          position={position}
          delay={delay}
          duration={duration}
          color={color}
        />
      );
    });
    
    setBubbles(newBubbles);
  }, [theme]); // Recreate bubbles when theme changes
  
  return <BubbleContainer>{bubbles}</BubbleContainer>;
};

export default AnimatedBackground; 